import axios from 'axios'

import qs from 'qs'
let instance = axios.create({
    headers: {'content-type': 'application/x-www-form-urlencoded'}
  });
  var baseUrl;
  if (document.domain.indexOf("localhost") > -1) {
    baseUrl = 'http://daojia-node-test.cycletech.info'
} else {

    baseUrl= window.location.protocol +"//"+window.location.host
}
  export default{
    baseUrl:baseUrl,
    // 
    gethome:params=> { return instance.get(`${baseUrl}/staff/index`,params).then(res => res.data) },

    login:params=> { return instance.post(`${baseUrl}/staff/login`,qs.stringify(params)).then(res => res.data).catch(err=>err) },
    // 
    unbind:params=> { return instance.get(`${baseUrl}/staff/unbind`,params).then(res => res.data) },
    // 
    orderCount:params=> { return instance.get(`${baseUrl}/staff/order/count`,params).then(res => res.data) },
    // 
    sendSMS:params=> { return instance.post(`${baseUrl}/sms/send`,qs.stringify(params)).then(res => res.data).catch(err=>err) },
    // 
    staffInfo:params=> { return instance.get(`${baseUrl}/staff/info`, params).then(res => res.data) },
    // 
    updatePassword:params=> { return instance.post(`${baseUrl}/staff/update/password`, qs.stringify(params)).then(res => res.data).catch(err=>err) },
    // 
    orderBookTime:(id, params)=> { return instance.post(`${baseUrl}/staff/order/booktime/${id}`, qs.stringify(params)).then(res => res.data) },
    // 
    finish:params=> { return instance.get(`${baseUrl}/staff/order/finish/`,params).then(res => res.data) },


    // 
    timestampToDay:(timestamp)=> {
        var date = new Date(timestamp*1000 );//10*1000131000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = date.getDate()<10?'0'+date.getDate():date.getDate() ;
        return Y+M+D;
    },
    payStatus: params=> { return instance.get(`${baseUrl}/staff/order/payStatus`,params).then(res => res.data) },
  }



