//
import wx from 'weixin-jsapi'
/*


@param{data}:
@param{cb}:
*/
let wexinPay = (data, cb, errorCb,cancelCb) => {
  let appId = data.appId;
  let timestamp = data.timeStamp;
  let nonceStr = data.nonceStr;
  let signature = data.signature;
  let packages = data.package;
  let paySign = data.paySign;
  let wxtimestamp = data.wxtimestamp
  let wxnonceStr = data.wxnonceStr
  console.log(data)
  wx.config({
    debug: false, // ,apialertpclogpc
    appId: appId, // 
    timestamp: wxtimestamp, // 
    nonceStr: wxnonceStr, // 
    signature: signature, // 1
    jsApiList: ['chooseWXPay'] // JSJS2
  });
  wx.ready(function () {

    wx.chooseWXPay({
      timestamp: timestamp, // jssdktimestamptimeStampS
      nonceStr: nonceStr, //  32 
      package: packages, // prepay_idprepay_id=***
      signType: 'MD5', // 'SHA1''MD5'
      paySign: paySign, // 
      success: function (res) {
        // 

        cb(res);
      },
      fail: function (res) {

        errorCb(res);
      },
      cancel:function(res){
        cancelCb(res)
      }
    });
  });
  wx.error(function (res) {
    console.log()
    // configerrorconfigdebugresSPA
    /*alert("config");*/
  });
}
export default wexinPay;


